import { Fragment, h, JSX } from '@stencil/core';
import { navContentStore } from '../../state/nav-content-store';
import { showBackButton } from '../../services/routing-manager';

export default function DrawerContent({
  routingKey,
  breakpoint,
  locale,
  receivedContent
}: {
  routingKey: string;
  breakpoint: number;
  locale: string;
  receivedContent: boolean;
}): JSX.Element {
  const { menu } = navContentStore.state;

  return (
    <Fragment>
      <div class="bottom-gradient"></div>
      <div class="drawer-content">
        <phn-drawer-header
          showBackButton={showBackButton(routingKey)}
          backButtonAnalyticsId={menu.id}
          breakpoint={breakpoint}
          route={routingKey}
          receivedContent={receivedContent}
          locale={locale}
        />
        <phn-router
          activeRoutingKey={routingKey}
          breakpoint={breakpoint}
          locale={locale}
          receivedContent={receivedContent}
        />
      </div>
    </Fragment>
  );
}
